.btn {
  background-color: #000;
  color: #fff;
  border-radius: 10px;
  outline: none;
  transition: background-color 0.3s linear;
  text-align: center;
  display: block;
  margin-top: 1rem;
  padding: 0.7rem;
  font-weight: bold;
}

.btn:hover {
  font-weight: bold;
  background-color: #ff5733;
  border-color: #000;
  color: #000;
  transition: background-color 0.3s linear;
}
